.header {
    margin-top: .5rem;
    color: #EB6864;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.header ul button {
    list-style: none;
    margin: 0;
    padding: .5rem 1rem;
    border-radius: 3px;
    border: none;
    background-color: white
}

.header ul button:hover,
.header ul button.active {
    background-color: #EB6864;
    color: white;
    cursor: pointer;
}

@media screen and (max-width: 500px) {
    .header {
        flex-direction: column;
        align-items: center;
    }
}